// @flow

/**
 * The normal d3 interpolateArray will just add on to the end of an array the new values if the
 * next array is longer than the original. However, we would like to have these values linearly
 * interpolated. More info:
 * http://hevi.info/do-it-yourself/interpolating-and-array-to-fit-another-size/
 */
export default function interpolateArraySize(data: number[], fitCount: number) {
  if (fitCount <= 1) return [data[0]]

  const springFactor = (data.length - 1) / (fitCount - 1)

  return Array(fitCount).fill(1).map((_, i) => {
    if (i === 0) return data[0]
    if (i === (fitCount - 1)) return data[data.length - 1]

    const tmp = i * springFactor
    const before = Number(Math.floor(tmp).toFixed())
    const after = Number(Math.ceil(tmp).toFixed())
    const atPoint = tmp - before

    return data[before] + ((data[after] - data[before]) * atPoint)
  })
}



